.work {

    &__container {
    }

    &__body {
        width: 1510px;
        height: 678px;
        display: grid;
        grid-template-rows: 50% 50%;
        grid-template-columns: 25% 25% 25% 25% ;
        margin: 0  auto 114px auto;
    }

    &__img {
        cursor: pointer;
    }
    &__img:hover {
        background: linear-gradient(rgba(255, 67, 67, 0.8),rgba(255, 67, 67, 0.8));
    }
}